.die {
    cursor: pointer;
    width: 100%;
    aspect-ratio: 1/1;
    border-radius: 10px;
    font-size: 20px;
    font-weight: 700;
    display: flex;
    align-items: center;
    justify-content: center;
    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
    transition: 200ms ease-in-out;
}
